import React, { useRef } from 'react';
import  './style.css';


const styles = {

  textView: {
    borderWidth: 1,
    borderColor: 'gray',
  },

  labelStyle: {
    position: 'absolute',
    right: 1,
    top:  0,
    fontSize: 12,
    textAlign: 'right',
    fontWeight: 'bold',
    fontFamily: 'OpenSans-Light',
  },

};


function DokuCell(props) {

  let cellSize ;
  let fontSize, labelSize=12;
  let colActive = 'gray';
  let colDim = 'transparent';
  cellSize = 20;
  fontSize=20;

  const inputEl = useRef(null);

  let styletemp = {
    borderWidth: 1,
  }

  function handleChange(text) {
    if (text > props.cells) {
      alert('You can only enter a value between 1 and ' + props.cells);
      return;
    }
    props.onChange(props.coord, text)
  }

  let [ci, cj] = props.coord.split('').map((t) => parseInt(t));
  for (let x of props.opsSet.coords) {
    let [xi, xj] = x.split('').map((t) => parseInt(t));
    if (xi == ci) {
      if (xj == (cj - 1)) {
        styletemp['borderLeftColor'] = colDim;
        //styletemp['borderLeftWidth'] = 0.1;
      }
      else {
        //styletemp['borderLeftColor'] = colActive;
        //styletemp['borderLeftWidth'] = 2;
      }
      if (xj == (cj + 1)) {
        styletemp['borderRightColor'] = colDim;
        //styletemp['borderRightWidth'] = 0.1;
      }
      else {
        //styletemp['borderRightColor'] = colActive;
        //styletemp['borderRightWidth'] = 2;
      }
    }
    if (xj == cj) {
      if (xi == (ci - 1)) {
        styletemp['borderTopColor'] = colDim;
        //styletemp['borderTopWidth'] = 0.1;
      }
      else {
        //styletemp['borderTopColor'] = colActive;
        //styletemp['borderTopWidth'] = 2;
      }
      if (xi == (ci + 1)) {
        styletemp['borderBottomColor'] = colDim;
        //styletemp['borderBottomWidth'] = 0.1;
      }
      else {
        //styletemp['borderBottomColor'] = colActive;
        //styletemp['borderBottomWidth'] = 2;
      }
    }
  }

  return (<div className="pure-u-1-3 cell-style" onClick={() => inputEl.current.focus()} style={styletemp}>
    {/*(props.coord == props.opsSet.coords[0]) && <div style={styles.labelStyle}>{props.opsSet.op.toLowerCase() + ' ' + props.opsSet.res}</div>*/}
    <input ref={inputEl} value={props.cellVal} name={props.coord} onChange={(e) => { handleChange(e.target.value) } } maxLength={1} onClick={() => props.onClick(props.coord)}/>
    { (props.coord == props.opsSet.coords[0]) && <label htmlFor={props.coord}  style={{backgroundColor:'inherit'}}> <sup>{props.opsSet.op.toLowerCase() + ' ' + props.opsSet.res}</sup> </label>}
  </div>);

}

export default DokuCell;