import React, { useState } from "react";
import axios from "axios";
import  './style.css';
import  '../../App.css';

function CvParse() {
  const [file, setFile] = useState(null);
  const [cvContent, setCvContent] = useState('');
  const [loading, setLoading] = useState(false);

  const UPLOAD_ENDPOINT =
    "http://localhost:8000/uploadcv";

  const handleSubmit = async e => {
    e.preventDefault();
    if (file == null) {
      alert('Please upload CV to parse first.');
      return;
    }
    setLoading(true);
    setCvContent('');
    console.log('in submitttttt');
    uploadFile(file);
    //console.log(res.data);
    //setCvContent(JSON.stringify(res.data, undefined, 4));
  };

  const uploadFile = async file => {
    const formData = new FormData();
    formData.append("file", file);

    axios.post(UPLOAD_ENDPOINT, formData, {
      headers: {
        'Access-Control-Allow-Origin' : '*',
        "content-type": "multipart/form-data"
      }
    })
    .then(response => {
      setLoading(false);
      setCvContent(JSON.stringify(response.data, undefined, 8));
    })
    .catch(error => {
      //setFile(null);
      setLoading(false);
      console.log(error);
      alert('Possible Error in network call to parse file.');
    });
    ;
  };

  const handleOnChange = e => {
    console.log(e.target.files[0]);
    setFile(e.target.files[0]);
  };

  return (
    <form className="pure-form form-style" onSubmit={handleSubmit}>
      <fieldset>
        <legend>Resume Parser</legend>
        <input style={{float:'left', margin: '2rem'}} type="file" onChange={handleOnChange} />
        <button style={{float:'right', margin: '2rem'}} class="button-secondary pure-button" type="submit">Parse CV</button>
       </fieldset>
       {loading ? <div style={{margin:'5rem'}}>{"Analysing CV....."}</div> : <div className="json-style"><code>{cvContent}</code></div>}
    </form>
  );
}

export default CvParse;
