import React from 'react'
import dokufrmServer from './dokufrmServer';
import  { DokuPanel } from '../components/DokuPanel';
import  { DokuGrid }  from '../components/DokuGrid';
import '../App.css';

const MathDoku = (props) => {

    return (
        <div className="mathdoku"> 
            <DokuPanel focusedCell={props.focusedCell} onSolveClick={props.onSolveClick} onSubmit={props.onSubmit} setCells={props.setCells} cells={props.cells} setClue={props.setClue} />
            <DokuGrid {...props}/>
        </div>
    );

}

export const MathDokuMain = dokufrmServer(MathDoku);
