import React from 'react';
//import axios from 'axios';
import {loadDoku, matchMaps} from '../common/utils'
import '../App.css';


const dokufrmServer = (WrappedComponent, url) => {

    return class extends React.Component {

        constructor(props) {
            super(props);
            this.state = {
                cells: 4,
                currIndex: 0,
                charMap: null,
                opsArr: null,
                error: null,
                pending: null,
                workingObj: null,
                clickedCell: null,
                isSolved: false,
            };
        }

        componentDidMount() {
            loadDoku(null,  { cells: this.state.cells, count: this.state.currIndex  } , this.convJSONtoMap, this.onLoadErr)
        }

        convJSONtoMap = (jsonData) => {
            
            let charMap = {}, opsArr = [];

            for (let obj of jsonData) {
              let charSet = [] //new Set();
              for (let coords of obj.l) {
                charMap[coords.c] = { val: coords.v, ops: opsArr.length } ;
                charSet.push(coords.c);
              }
              opsArr.push({ op: (obj.o == '/') ? '÷' : obj.o , res: obj.r, coords: charSet });
            }

            this.setState((prevstate) => ({
                pending: false,
                charMap: charMap,
                opsArr: opsArr,
                workingObj: {},
                currIndex: prevstate.currIndex + 1
                }))
        }
    

        onClick = (coord) => {
            this.setState ({
                clickedCell : coord
            })
        }

        onLoadErr = (err) => {
            this.setState (
            {
                error : err,
            })
        }

        onSubmit = () => {
            let cells = this.state.cells
            this.resetState();
            this.setState ((prevState) => ({
                cells : cells,
                //currIndex : prevState.currIndex + 1,
            }))

            loadDoku(null, { cells : cells, count : this.state.currIndex }, this.convJSONtoMap, this.onLoadErr)
        }

        setWorkingCell = (coord, val) => {
            this.setState ((state) => ({
                workingObj : {...state.workingObj, [coord]: val}
            }))
        }

        setClue = () => {
            let coord = this.state.clickedCell;
            let val = this.state.charMap[coord].val;
            this.setWorkingCell(coord, val);
        }

        setCells = (e) => {
            this.resetState();
            let cells = e.target.value
            this.setState ((prevState) => ({
                cells : cells,
                //currIndex : prevState.currIndex + 1,
            }))
            loadDoku(null, { cells : cells, count : this.state.currIndex }, this.convJSONtoMap, this.onLoadErr)
        }

        onSolveClick = () => {
            this.setState ({
                isSolved : true
            })
        }

        resetState = () => {
            this.setState((state) => ({
                cells: null,
                charMap: null,
                opsArr: null,
                error: null,
                pending: null,
                workingObj: null,
                isSolved: false,
            }));
        }

        render() {

            return (this.state.charMap) ? 
                    <WrappedComponent 
                    cells={this.state.cells} 
                    charMap={this.state.charMap} 
                    workingObj={this.state.workingObj} 
                    opsArr={this.state.opsArr} 
                    isSolved={this.state.isSolved} 
                    currIndex={this.state.currIndex}
                    pending={this.state.pending}
                    error={this.state.error}
                    focusedCell={this.state.clickedCell}
                    setClue={this.setClue}
                    onSubmit={this.onSubmit}
                    setCells={(x,y) => this.setCells(x,y)} 
                    setWorkingCell={(x,y) => this.setWorkingCell(x,y)} 
                    onClick={(x) => this.onClick(x)} 
                    onSolveClick={this.onSolveClick}/>
                    : <div className="fetch-overlay"> {!this.state.error ? <h3>Please wait while a new puzzle is created.....</h3> : <p> Sorry...There was an error in fetching Doku from server</p>} </div>;
        }
    }

}


export default dokufrmServer