import React from 'react';
import  './style.css'



const DokuPanel = (props) => {


  function fetchNew(numCells) {
    props.onSubmit();
  }


  return <div className="form-style">
          <div className="pure-form tool-bar">
            <button type="submit" className="pure-button tool-item" onClick={() => { if (props.focusedCell !== undefined && props.focusedCell != '') props.setClue(); }}>Clue</button>
            <button  type="submit" className="pure-button tool-item" onClick={() => { props.onSolveClick(); }}>Solve</button>
              <select  className="tool-item" name="cellsize"  value={props.cells} onChange={props.setCells}>
                  <option value="4">4</option>
                  <option value="6">6</option>
                  <option value="9">9</option>
              </select>
              <label  style={{fontSize:'0.8rem',  paddingTop: '0.5rem', height:'2rem',  marginLeft: '-0.9rem'}} for="cellsize">cells</label>
              <button  type="submit" class="button-secondary pure-button tool-item" style={{width:'9rem',}} onClick={() => { let cell = props.cells; console.log('props.cells' + props.cells); fetchNew(cell); }}>New Puzzle</button>
          </div>
        </div>;    

  
}

export default DokuPanel;