

  export function matchMaps(charMap, workingObj, cells, coord, val) {
    let matched=true;
    // when we come here, workingObj will have one less than charMap as workingobj for current coors has not yet been updated in state!
    if ( (Object.keys(charMap).length - 1) <= Object.keys(workingObj).length ) {
      for (let x of Object.keys(workingObj) ) {
          //let coord = i + '' + j;
          if ((x != coord) && (charMap[x].val !=  workingObj[x])) {
            matched = false;
            return matched;
          }
      }
      if (charMap[coord].val != val) {
        matched = false;
        return matched;
      }
    }
    else
      matched = false;
    return matched;
  }



  export const loadDoku = (url, params, callback, err) => {

    let cont = 'https://1dyfre.deta.dev';  // change to mask o=dok url later!!!! 
    //let cont = 'http://localhost:8080'; 
    fetch(cont + '/api/mathdoku', {                  //
      method: 'POST',
      cache: 'no-cache',
      headers: { 'Content-Type':'application/json',  'Access-Control-Allow-Origin':'*', },
      body: JSON.stringify(params),
    })
    .then(res =>
      {
          if (!res.ok) {
              throw Error(res.status + '; ' + res.statusText)
          }
          return res.json() 
      })
   .then(res => {
        callback(res)
     })
    .catch(e => {
      err(e);
      alert(e );
    })

}