import React, { useState, useEffect } from 'react';
import {  matchMaps } from '../../common/utils';
import { DokuCell } from '../DokuCell';
import  './style.css'


const styles = {

  styleContainer: {
    marginTop: 100,
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },

  styleTick: {
    textAlign: 'center',
    color: 'teal',
  },

  overlay: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    position: 'absolute',
    width: '50%',
    height: '50%',
    elevation: 4,
  }


};


class DokuGrid extends React.Component {

  constructor(props) {
    super(props);
  }

  onChangeCell = (coord, val) => {
    if (matchMaps(this.props.charMap, this.props.workingObj, this.props.cells, coord, val)) {
      this.props.setSolved(true);
      return;
    }
    this.props.setWorkingCell(coord, val);

    let [row, col] = coord.split('');
    for (let x = 0; x < this.props.cells; ++x)
      if (x !== row)
        if (this.props.workingObj[x + '' + col] == val) {
          alert('You have already used ' + val + ' in row ' + (x + 1) + ' and column ' + (parseInt(col) + 1));
          return;
        }
    for (let y = 0; y < this.props.cells; ++y)
      if (y != col)
        if (this.props.workingObj[row + '' + y] == val) {
            alert('You have already used ' + val + ' in row ' + (parseInt(row) + 1) + ' and column ' + (y + 1));
            return;
        }

  }

  
  onClick = (coord) => {
    this.props.onClick(coord);
  }
  

  componentDidMount() {
    let cells = this.props.cells;
  }

  renderGrid() {
    

    let grid=[];
    let solved = (this.props.isSolved) ? true: false; // add workimngobj to charMap compaison also here
    for (let i=0; i< this.props.cells; i++) {
      let rows=[];
      for (let j=0; j< this.props.cells; j++) {
        let cellValue = solved ? this.props.charMap[i + '' +j].val : (this.props.workingObj[i + '' +j] !== undefined ? this.props.workingObj[i + '' +j] : '') ;
        rows.push(<DokuCell  key={i + '' +j} coord= {i + '' +j} cellVal={cellValue + ''} onChange={this.onChangeCell} onClick={() => this.onClick(i + '' +j)} clue={this.props.clue} cells={this.props.cells} opsSet={ this.props.opsArr[this.props.charMap[i+''+j].ops] } />)
      }
      grid.push(<div key={i+''} >{rows}</div>);
    }
    return <div className="pure-g gridcolumn">{grid}</div>;
  }

  render () {

    let retView = <div className="dokugrid" style={{ padding: 10}}>
                    {this.props.error && <div style={styles.styleContainer}><div style={{fontFamily: 'OpenSans-Regular'}}>{this.props.error}</div></div>}
                    {this.props.pending && <div style={styles.styleContainer} size="large" color="tomato"/>}
                    {!(Object.keys(this.props.opsArr).length === 0) && this.renderGrid()}
                    {this.props.isSolved && <div style={styles.overlay}><span style={styles.styleTick}> <h1>&#10003;</h1>  </span></div>} 
                </div>

    return retView;
  }

}

export default DokuGrid;